import { useWeb3React } from '@web3-react/core';
import { useEffect, useMemo, useState } from 'react';
import { useMoralis } from 'react-moralis';

function useNftTransfers(address: string, id: string) {
  const [transfers, setTransfers] = useState<any>([]);
  const [isLoading, setIsLoading] = useState(false);
  const { Moralis } = useMoralis();

  const { chainId } = useWeb3React();

  const chain = useMemo(() => {
    if (chainId === 1) return 'eth';
    else return 'rinkeby';
  }, [chainId]);

  const getNftsFromAddress = async () => {
    try {
      setIsLoading(true);
      // @ts-ignore
      const data = await Moralis.Web3API.token.getWalletTokenIdTransfers({
        address,
        token_id: id,
        chain: 'eth',
      });

      setTransfers(data.result);
      setIsLoading(false);
    } catch (error) {
      console.log(error);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    getNftsFromAddress();
  }, [chain, address]);

  return { transfers, isLoading };
}

export default useNftTransfers;
