import { Contract } from '@ethersproject/contracts';

import { getContract, getSignContract, getUnsignContract } from '@utils/contracts';
import ERC20_ABI from '@modules/web3/abi/ERC20.json';
import RADNFT_ABI from '@modules/web3/abi/RadNft.json';
import ERC721_ABI from '@modules/web3/abi/ERC721.json';
import NFT_STAKING_ABI from '@modules/web3/abi/NftStaking.json';
import NFT_MARKET_ABI from '@modules/web3/abi/NftMarket.json';
import NFT_MARKET_WITH_AUCTION_ABI from '@modules/web3/abi/NftMarketWithAuction.json';
import POAP_ABI from '@modules/web3/abi/Poap.json';
import LINKS_NFT_ABI from '@modules/web3/abi/LinksNFT.json';
import STAKING_ABI from '@modules/web3/abi/Staking.json';
import { useWeb3React } from '@web3-react/core';
import { Address, CONTRACTS, NFTS } from '@utils/constants';
import { getAddressByChainId } from '@utils/index';
import { AddressZero } from '@ethersproject/constants';
import { Web3Provider, JsonRpcProvider } from '@ethersproject/providers';

function useContractByChainId(address: Address, ABI: any, withSignerIfPossible = true): Contract {
  const { library, account, chainId } = useWeb3React();
  return getContract(
    getAddressByChainId(address, chainId || Number(process.env.REACT_APP_DEFAULT_CHAIN_ID)),
    ABI,
    library,
    withSignerIfPossible && account ? account : undefined,
  );
}

// getSignContract
// getUnsignContract

function useContract(address: string, ABI: any, withSignerIfPossible = true): Contract {
  const { library, account } = useWeb3React();
  return getContract(address, ABI, library, withSignerIfPossible && account ? account : undefined);
}

function useSignContract(address: string, ABI: any, withSignerIfPossible = true): Contract {
  const { library, account } = useWeb3React();
  return getSignContract(
    address,
    ABI,
    library,
    withSignerIfPossible && account ? account : undefined,
  );
}

function useUnsignContract(address: string, ABI: any, withSignerIfPossible = true): Contract {
  const { library } = useWeb3React();
  return getUnsignContract(address, ABI, library);
}

function useGodwokenContract(address: string, ABI: any, withSignerIfPossible = true): Contract {
  const { library, account } = useWeb3React();
  const ethersWeb3Provider = new Web3Provider(window.ethereum);
  const signer = ethersWeb3Provider.getSigner();
  return new Contract(address, ABI, signer);
  // const signer = contract.connect(library.getSigner());
  // return signer;
}

export const useTokenContract = (tokenAddress: string, withSignerIfPossible?: boolean): Contract =>
  useContract(tokenAddress, ERC20_ABI, withSignerIfPossible);

export const useRadNftContract = (withSignerIfPossible?: boolean): Contract =>
  useContractByChainId(NFTS.Rad.address, RADNFT_ABI, withSignerIfPossible);

export const useERC721Contract = (tokenAddress: string, withSignerIfPossible?: boolean): Contract =>
  useContract(tokenAddress, ERC721_ABI, withSignerIfPossible);

export const usePoapContract = (tokenAddress: string, withSignerIfPossible?: boolean): Contract =>
  useContract(tokenAddress, POAP_ABI, withSignerIfPossible);

export const usePoapLinksContract = (
  tokenAddress: string,
  withSignerIfPossible?: boolean,
): Contract => useContract(tokenAddress, LINKS_NFT_ABI, withSignerIfPossible);

export const usePoapLinksSignContract = (
  tokenAddress: string,
  withSignerIfPossible?: boolean,
): Contract => useSignContract(tokenAddress, LINKS_NFT_ABI, withSignerIfPossible);

export const usePoapLinksUnsignContract = (
  tokenAddress: string,
  withSignerIfPossible?: boolean,
): Contract => useUnsignContract(tokenAddress, LINKS_NFT_ABI, withSignerIfPossible);

export const useNftStakingContract = (withSignerIfPossible?: boolean): Contract =>
  useContractByChainId(CONTRACTS.NftsStaking.address, NFT_STAKING_ABI, withSignerIfPossible);

export const useNftMarketContract = (withSignerIfPossible?: boolean): Contract =>
  useContractByChainId(CONTRACTS.NftsMarket.address, NFT_MARKET_ABI, withSignerIfPossible);

export const useNftMarketWithAuctionContract = (withSignerIfPossible?: boolean): Contract =>
  useContractByChainId(
    CONTRACTS.NftMarketWithAuction.address,
    NFT_MARKET_WITH_AUCTION_ABI,
    withSignerIfPossible,
  );

export const useStakingContract = (withSignerIfPossible?: boolean): Contract =>
  useContractByChainId(CONTRACTS.Staking.address, STAKING_ABI, withSignerIfPossible);

export default useContract;
