import { Tokens, TOKENS_IDS } from '@utils/constants';
import { useEffect, useMemo, useState } from 'react';

function useStakingTokenPriceInUsd(poolId: number) {
  const [tokenPrice, setTokenPrice] = useState(0);
  const [isLoading, setIsLoading] = useState(false);

  const stakingTokensId = useMemo(() => {
    return TOKENS_IDS[poolId as keyof Tokens];
  }, [poolId]);

  const getTokenPrice = async () => {
    try {
      setIsLoading(true);
      const request = await fetch(
        `https://api.coingecko.com/api/v3/simple/price?ids=${stakingTokensId}&vs_currencies=usd`,
      );
      const res = await request.json();
      setTokenPrice(res[stakingTokensId].usd);
      setIsLoading(false);
    } catch (error) {
      console.log(error);
      setIsLoading(false);
    }
  };

  useEffect(() => {
    getTokenPrice();
  }, [stakingTokensId]);

  return { tokenPrice, isLoading };
}

export default useStakingTokenPriceInUsd;
